import $http from '../../untils/search.js'
export default {
	data() {
		return {
			searchKey: [],
			ipt: '',
			searchClose: true,
			search_item: []
		}
	},
	onShow() {
		this.searchKey = uni.getStorageSync('searchLocal')
	},
	methods: {
		clearKey: function() {
			var that = this;
			uni.showModal({
				title: '提示',
				content: '点击确定将删除所有历史信息，确定删除吗？',
				success: function(res) {
					if (res.confirm) {
						that.searchKey = [];
						uni.setStorage({
							key: 'searchLocal',
							data: that.searchKey
						});	
					}
				}
			});

		},
		search(){
			let timeOut = null
			if(!timeOut){
				clearTimeout(timeOut)
				this.search_item = []
				timeOut = setTimeout(() => {
					$http(this.ipt).then(res => {
						let el = [];
						let newData = res.data.result.songs.reduce((el, cur) => {
							console.log(cur);
							if(!el.includes(cur.name)){
								el.push(cur.name)
								console.log(el)
								this.search_item.push(cur)
							}
							return el
						}, [])
						//console.log(this.search_item)
					})
				},1000)
			}
		},
		search_history(params){
			this.searchKey.push(params)
			uni.setStorage({
				key: 'searchLocal',
				data: this.searchKey
			});	
		}
	}
}
